/* Menu css */

#card{
    background-color: rgb(255, 255, 255);
    width: 25rem;
    opacity: 100%;
    padding: 15px;
  }
  #card:hover{
    transition: 0.3s;
    color: white;
    background-color: rgb(255, 113, 113);
    opacity: 100%;
  }
  #card-title{
    font-size: 26px;
    font-weight: 600;
  }
  #card-img{
    width: 100%;
    height: 80%;
  }
  .checked{
    color:orange
  }
  #card-text{
    font-size: 19px;
   
  }
  #price{
    margin-left: 20px;
    font-size: 20px;
    font-weight: bold;
    color: rgb(109, 69, 1);
  }
  #dish-heading-1{
    font-size: 26px;
    color: rgb(243, 175, 49);
  }
  #dish-heading-2{
    font-size: 31px;
    font-weight: 560;
    color: rgb(243, 175, 49);
  }
  #btn-order:hover{
    background-color:cyan;
    color: red;
    transition: 0.3s;
  }
  @media screen and (min-width: 800px) {
   #card{
     margin-left: 20px;
   }
  }