
/* Navbar css */

.navbar-brand{
  font-size: 28px;

}
.nav-item{
  border-radius: 10px;
  font-size: 17px;
}
.nav-item:hover{
  background-color: rgb(163, 177, 35);
  padding: 0px;
  transition: 0.5s;
}

/* Carousel css */
.carousel {
  margin-top: 60px;
  margin-bottom: 0px;
}
.carousel-inner{
  margin-top: 0.5px;
}
.carousel-item {
  background: no-repeat center center scroll;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.banner-heading1{
  font-size: 28px;
  color: orange;
}
.banner-heading2{
  font-size: 58px;
  font-weight: bold;
  color: rgb(121, 121, 255);
}
.banner-txt{
  color: orange;
}
.banner-img{
  width: 80%;
  height: 100%;
}
.banner-info{
  margin-left: 170px;
  margin-top: 100px;
}
.banner-btn{
  color: cyan;
  padding: 10px;
  padding-right: 20px;
  padding-left: 20px;
  background-color: rgb(102, 4, 141);
}
.banner-btn:hover{
  color: red;
  background-color: cyan;
  transition: .03s;
}
@media screen and (min-width: 800px) {
  .carousel-item{
    height: 75vh;
  }
 }
 @media screen and (max-width: 800px) {
  .banner-info{
    display: none;
  }
  .carousel-item{
    margin-left: 30px;
  }
 }

 .banner-img{
  animation: App-logo-spin infinite 20s linear;

}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



