/*About css*/
#about-heading-1{
  font-size: 26px;
}
#about-heading-2{
  font-size: 31px;
  font-weight: 560;
  
}
.about-img{
  width: 80%;
  height: 100%;
}
.about-txt{
  font-size: 28px;
 
}
